html,
body {
    height: 100vh;
}

.rbc-calendar {
    height: 100%;
}

/* Modal */
.ReactModalPortal>div {
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .3s ease-in-out;
    z-index: 999;
}

.modal-background {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: #fff;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 640px;
    max-width: 500px;
    outline: none;
    padding: 10px;
}

/* Datepicker */
.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border: 0px solid gray !important;
}

/* FABS */
.fab {
    border-radius: 100%;
    position: fixed;
    bottom: 25px;
    padding: 20px;
    right: 60px;
}

.fab-danger {
    border-radius: 100%;
    position: fixed;
    bottom: 25px;
    padding: 17px 20px;
    left: 60px;
}

/* Spinner */
.spinner  {
    margin-top: 10rem;
}